// 储存数据到vuex

import { getPhoneLogin } from '@/request/api/home';
import { getLiked, getMusicLyric } from '@/request/api/item';
import { createStore } from 'vuex'

export default createStore({
    // 存数据
    state: {
        // 播放列表
        playList: [{
            al: {
                picUrl: "https://p1.music.126.net/RWIGyShmnjmUxizXco6fVg==/109951168505830245.jpg",
            },
            ar: [{
                name: 'HOYO-MiX'
            }],
            name: '太空漫步 Space Walk',
            id: 2034615687,
            // alia: [
            //     "TV动画「BanG Dream! It's MyGO!!!!!」#7 插曲"
            // ]
        }],
        // 默认下标为0
        playListIndex: 0,
        // 暂停按钮显示
        isbtnShow: true,
        // 详情页显示
        detailShow: false,
        // 歌词
        lyricList: {},
        // 歌曲当前时间
        currentTime: 0,
        // 歌曲总时长
        duration: 0,
        // 判断是否登录
        isLogin: false,
        // 用户信息
        user: {},
        // 每日推荐
        recommend: {},
        // 用户喜欢的音乐
        userLike: {}
    },
    // 方法
    mutations: {
        updateIsbtnShow: function (state, value) {
            state.isbtnShow = value
        },
        pushPlayList: function (state, value) {
            state.playList.push(value)
        },
        updatePlayList: function (state, value) {
            state.playList = value
            // console.log(state.playList);
        },
        updatePlayListIndex: function (state, value) {
            state.playListIndex = value
        },
        updateDetailShow: function (state) {
            state.detailShow = !state.detailShow
        },
        updateLyricList: function (state, value) {
            state.lyricList = value
        },
        updateCurrentTime: function (state, value) {
            state.currentTime = value;
        },
        updateDuration: function (state, value) {
            state.duration = value
        },
        updateIsLogin: function (state) {
            state.isLogin = true
        },
        updateUser: function (state, value) {
            state.user = value
        },
        updateLiked: function (state, value) {
            state.userLike = value
        },

    },
    actions: {
        getLyric: async function (context, value) {
            let res = await getMusicLyric(value)
            context.commit("updateLyricList", res.data.lrc)
            // console.log(res);
        },
        getLogin: async function (context, value) {
            let res = await getPhoneLogin(value)
            // console.log(res);
            return res
        },
        getUserLiked: async function (context, value) {
            if (localStorage.getItem("cookie")) {
                let res = await getLiked(value)
                context.commit("updateLiked", res.data.ids)
                console.log(res);
            }
        }
    }
})