<template>
  <div>
    <!-- 顶部组件 -->
    <div class="mytop">
      <van-icon name="wap-nav" size="24" @click="showPopup" color="#fff" />
      <div class="user">
        <img class="myhead" :src="user.avatarUrl" alt="" />
        <span style="font-weight: 600" class="name"> {{ user.nickname }}</span>
      </div>
      <van-icon
        name="search"
        size="24"
        color="#fff"
        @click="$router.push('/search')"
      />
    </div>

    <!-- <div class="">
      <img class="myhead1" :src="user.avatarUrl" alt="" />
    </div> -->

    <div class="bg">
      <img :src="user.backgroundUrl" alt="" />
    </div>

    <div class="useritemList">
      <span class="icspan1">我的歌单</span>
      <div class="userMusicList" v-for="item in userMusicList" :key="item">
        <router-link
          class="van-swipe-item"
          :to="{ path: '/MusicItem', query: { id: item.id } }"
        >
          <div class="ml">
            <img class="mlHead" :src="item.coverImgUrl" alt="" />
            <div class="ml1">
              <span class="mlName">{{ item.name }}</span>
              <span class="mltrackCount"
                >{{ item.trackCount }} 首 · {{ item.playCount }} 次播放</span
              >
            </div>
          </div>
        </router-link>
      </div>
    </div>

    <!-- 侧边弹出 -->

    <van-popup
      v-model:show="showLeft"
      position="left"
      :style="{ width: '85%', height: '100%' }"
    >
      <!-- 侧边导航 -->

      <Sidebar></Sidebar>
    </van-popup>

    <!-- 标签栏 -->

    <Tabbar></Tabbar>
  </div>
</template>

<script>
import Tabbar from "../components/Tabbar.vue";
import Sidebar from "../components/Sidebar.vue";

import { ref } from "vue";
import { getUserMusicList } from "../request/api/item";

export default {
  components: {
    Tabbar,
    Sidebar,
  },
  mounted() {
    this.getUser();
    this.getUserMusic();
  },
  data() {
    return {
      user: {},
      userMusicList: [],
    };
  },

  methods: {
    getUser() {
      const user = JSON.parse(localStorage.getItem("user"));
      this.user = user;
      // console.log(user);
    },
    getUserMusic: async function () {
      let res = await getUserMusicList(this.user.userId);
      // console.log(res);
      this.userMusicList = res.data.playlist;
      // console.log(this.userMusicList[0]);
    },
  },

  setup() {
    const showLeft = ref(false);
    const showPopup = () => {
      showLeft.value = true;
    };
    return {
      showLeft,
      showPopup,
    };
  },
};
</script>

<style>
/* 顶部组件 */
.mytop {
  /* position: absolute; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}
.mytop .van-icon-search {
  margin-right: 10px;
}

:root:root {
  /* 侧边弹出 */
  --van-popup-background: rgb(245, 245, 245);
}

.mytop .myhead {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.mytop .user {
  display: flex;
  align-items: center;
}
.mytop .user span {
  margin-left: 10px;
}

.useritemList {
  position: absolute;
  top: 378px;
  width: 100%;
  background-color: #fff;
  padding-bottom: 130px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.bg img {
  top: 0;
  /* top: 68px; */
  position: absolute;
  width: 100%;
  z-index: -1;
}
.icspan1 {
  margin: 10px 20px;
  color: black;
  font-size: 23px;
  font-weight: 600;
  display: flex;
  justify-content: center;
}
.ml {
  display: flex;
  align-items: center;
}
.mlHead {
  width: 60px;
  border-radius: 20px;
}
.mlName {
  font-weight: 600;
}
.mltrackCount {
  font-size: 14px;
  color: #717171;
}

.ml1 {
  margin-left: 16px;
  display: flex;
  flex-direction: column;
}
.userMusicList {
  margin: 12px;
  font-size: 18px;
  display: flex;
}
</style>