<template>
  <div>
    <van-grid :border="false">
      <van-grid-item @click="recommend">
        <img src="../assets/icon/today.png" alt="" />
        <span>每日推荐</span>
      </van-grid-item>
      <van-grid-item @click="playMusic">
        <img src="../assets/icon/MV.png" alt="" />
        <span>私人FM</span>
      </van-grid-item>
      <van-grid-item @click="persona">
        <img src="../assets/icon/歌单.png" alt="" />
        <span>歌单</span>
      </van-grid-item>
      <van-grid-item @click="toplist">
        <img src="../assets/icon/rank_fill.png" alt="" />
        <span>排行榜</span>
      </van-grid-item>
    </van-grid>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { geteFM, geteSong } from "../request/api/home";
export default {
  data() {
    return {
      FM: [],
      id: "",
      itemList: {},
    };
  },
  mounted() {
    this.getUserFM();
  },
  methods: {
    recommend() {
      if (localStorage.getItem("cookie")) {
      this.$router.push("/recommenditem");
    } else {
      alert("请先登录");
      return;
    }
    },
    toplist() {
      this.$router.push("/toplist");
    },
    persona() {
      this.$router.push("/persona");
    },

    getUserFM: async function () {
      let res = await geteFM();
      this.FM = res.data.data;
      // console.log(this.FM);

      let item = this.FM.map(async (song) => {
        let res = await geteSong(song.id);
        let res1 = res.data.songs[0];
        // console.log(res1);
        return res1;
      });
      this.itemList = await Promise.all(item);
      // console.log(this.itemList);
    },
    playMusic: function () {
      this.updatePlayList(this.itemList);
    },

    ...mapMutations(["updatePlayList"]),
  },
};
</script>

<style>
.van-grid {
  /* border-bottom: 1px solid #ccc; */
}
.van-grid-item img {
  object-fit: cover;
  width: 38px;
}
.van-grid-item span {
  color: #757575;
  font-size: 13px;
}
</style>