<template>
  <van-tabbar class="bottomtop"
    v-model="active"
    active-color="RGB(255,58,58)"
    inactive-color="RGB(188,188,188)"
  >
    <van-tabbar-item to="/home" name="home" icon="music-o"
      >推荐</van-tabbar-item
    >
    
    <van-tabbar-item to="/my" name="my" icon="contact-o">我的</van-tabbar-item>
  </van-tabbar>
</template>

<script>
import { ref } from "vue";

export default {
  setup() {
    const active = ref("");
    return { active };
  },
  computed: {
    active() {
      return this.$route.name;
    },
  },
};
</script>

<style>
:root:root {
  --van-tabbar-height: 75px;
  --van-tabbar-item-icon-size: 30px;
}
.bottomtop {
  position: fixed;
  bottom: 0;
}
.van-tabbar{
  height: 60px;
}
</style>