import {
    Search, Sidebar, SidebarItem, Tabbar, TabbarItem, Icon, Popup, Cell, CellGroup, Button, Swipe, SwipeItem, Grid, GridItem, Tag, List, Slider, Form, Field,
} from 'vant'

let vantpluins = [
    Search,
    Sidebar, SidebarItem,
    Tabbar, TabbarItem,
    Icon,
    Popup,
    Cell, CellGroup,
    Button,
    Swipe, SwipeItem,
    Grid, GridItem,
    Tag,
    List,
    Slider,
    Form, Field,
]


export default function getVant(app) {
    vantpluins.forEach((item) => {
        return app.use(item)
    })
}