<template>
  <div class="footmusic" :style="componentStyle">
    <div class="footerleft" @click="updateDetailShow">
      <img class="cdbg" src="../../assets/cd.png" alt="" />
      <img :src="playList[playListIndex].al.picUrl" alt="" />
      <span>{{ playList[playListIndex].name }}</span>
    </div>
    <div class="footerright">
      <img
        class="play"
        src="../../assets/icon/播放2.png"
        alt=""
        @click="play"
        v-if="isbtnShow"
      />
      <img
        class="play"
        src="../../assets/icon/暂停.png"
        alt=""
        @click="play"
        v-else
      />
      <van-icon name="bars" size="30px" color="#3e475b" />
    </div>
    <audio
      ref="audio"
      :src="`https://music.163.com/song/media/outer/url?id=${playList[playListIndex].id}.mp3`"
      :volume="defaultVolume"
    ></audio>

    <van-popup
      v-model:show="detailShow"
      position="bottom"
      :style="{ height: '100%' }"
      teleport="#app"
    >
      <MusicDetails
        :musicDetail="playList[playListIndex]"
        :isbtnShow="isbtnShow"
        :addDuration="addDuration"
        @updateDetailShow="updateDetailShow"
        @play="play"
      ></MusicDetails>
    </van-popup>
  </div>
</template>

<script>
// vue3写法
// import { computed } from "vue";
// import { useStore } from "vuex";

// export default {
//   setup() {
//     const store = useStore();

//     const playList = computed(() => store.state.playList);
//     const playListIndex = computed(() => store.state.playListIndex);

//     return {
//       playList,
//       playListIndex,
//     };
//   },
// };

// vue2写法
import MusicDetails from "./MusicDetails.vue";

import { mapState, mapMutations } from "vuex";
import { geteMusicUrl } from "../../request/api/home";
// import { ref } from "vue";

export default {
  components: {
    MusicDetails,
  },
  data() {
    return {
      interVal: 0,
      defaultVolume: 0.1, // 默认音量为10%
      // id: 86771,
      // url: "",
    };
  },

  computed: {
    currentPageName() {
      // 获取页面名称
      return this.$route.name;
    },
    componentStyle() {
      // 根据当前页面状态计算组件样式
      if (this.currentPageName === "login") {
        return { display: "none" };
      } else if (
        this.currentPageName === "home" ||
        this.currentPageName === "my" ||
        this.currentPageName === "find" ||
        this.currentPageName === "video"
      ) {
        return { bottom: "60px" };
      }
    },

    ...mapState(["playList", "playListIndex", "isbtnShow", "detailShow"]),
  },
  mounted() {
    // this.getUrl();
    // console.log(this.$refs);
    this.$store.dispatch("getLyric", this.playList[this.playListIndex].id);
    // this.updateTime();
    this.$store.dispatch("getUserLiked");
  },
  updated() {
    this.$store.dispatch("getLyric", this.playList[this.playListIndex].id);
    this.addDuration();
    this.updateTime();
    this.$store.dispatch("getUserLiked");
  },
  methods: {
    // getUrl: async function getUrl() {
    //   let res = await geteMusicUrl(this.id);
    //   // console.log(res);
    //   let res2 = JSON.stringify(res.data.data[0].url);
    //   this.url = res2;
    //   console.log(this.url);
    // },
    play: function () {
      // 判断音乐是否播放
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
        this.updateIsbtnShow(false);
        this.updateTime(); //播放调用函数进行传值
      } else {
        this.$refs.audio.pause();
        this.updateIsbtnShow(true);
        clearInterval(this.interVal); //暂停清除定时器
      }
    },
    addDuration: function () {
      this.updateDuration(this.$refs.audio.duration);
    },
    updateTime: function () {
      this.interVal = setInterval(() => {
        this.updateCurrentTime(this.$refs.audio.currentTime);
      }, 50);
    },
    ...mapMutations([
      "updateIsbtnShow",
      "updateDetailShow",
      "updateCurrentTime",
      "updateDuration",
    ]),
  },
  // 实现切换歌曲
  watch: {
    // 监听发生改变自动播放音乐
    playListIndex: function () {
      this.$refs.audio.autoplay = true;
      // 如果是暂停状态，改变图标
      if (this.$refs.audio.paused) {
        this.updateIsbtnShow(false);
      }
    },
    playList: function () {
      this.$refs.audio.autoplay = true;
      if (this.$refs.audio.paused) {
        this.updateIsbtnShow(false);
      }
    },
    currentTime: function (newValue) {
      if (newValue === this.duration) {
        if (this.playListIndex === this.playList.length - 1) {
          this.updatePlayListIndex(0);
          this.play();
        } else {
          this.updatePlayListIndex(this.playListIndex + 1);
        }
      }
    },
  },
};
</script>


<style scoped>
.footmusic {
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 60px;
  justify-content: space-between;
  background-color: #fff;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3);
}
.footmusic .footerleft {
  display: flex;
  padding: 0 38px;
  height: 100%;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.footmusic .footerright {
  margin: 0 20px;
  display: flex;
  align-items: center;
}
.footmusic .footerleft span {
  width: 100%;
  margin-left: 30px;
  font-size: 18px;
  color: #3e475b;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
}
.footmusic .footerleft img {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}
.footmusic .footerleft .cdbg {
  width: 54px;
  height: 54px;
  position: absolute;
  left: 29px;
  animation: rotateheadimg 26s linear infinite;
}
.cdbg_active {
  animation-play-state: running;
}
.cdbg_pauesd {
  animation-play-state: paused;
}
@keyframes rotateheadimg {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.footmusic .footerright .play {
  margin-right: 20px;
  width: 26px;
}
.van-popup {
  background: #e9e9e9;
}
</style>