<template>
  <div class="login">
    <img v-show="isShow" :src="this.img" alt="" />
    <span @click="clickShow()" v-show="isShow" class="qrtext1"
      >返回密码登录</span
    >
    <div v-show="!isShow">
      <van-form>
        <van-cell-group inset>
          <van-field
            v-model="phone"
            type="number"
            name="phone"
            label="+86"
            placeholder="请输入手机号"
          />
          <van-field
            v-model="password"
            type="password"
            name="password"
            label="密码"
            placeholder="请输入密码"
          />
        </van-cell-group>
        <div style="margin: 16px">
          <van-button
            @click="login"
            color="red"
            round
            block
            type="primary"
            native-type="submit"
          >
            登录
          </van-button>
          <div style="display: flex; justify-content: space-between">
            <span class="qrtext" @click="clickShow(), qrLogin()"
              >二维码登录</span
            >
            <span class="yktext" @click="ykLogin">以游客登录</span>
          </div>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import {
  getKey,
  getQr,
  getQrcheck,
  getLoginStatus,
  getPhoneLogin,
  getUser,
} from "../request/api/home";

export default {
  data() {
    return {
      qrKey: "",
      img: "",
      isShow: false,
      cookie: "",
      phone: "",
      password: "",
    };
  },

  mounted() {
    this.$store.dispatch("getUserLiked");
    this.inLogin();
  },
  methods: {
    clickShow() {
      this.isShow = !this.isShow;
    },
    ykLogin() {
      this.$router.push("/home");
    },
    inLogin() {
      let res = localStorage.getItem("cookie");
      if (res !== null) {
        this.$router.push("/home");
      }
      // console.log(res);
    },

    login: async function () {
      // let res = await this.$store.dispatch("getLogin", {
      //   phone: this.phone,
      //   password: this.password,
      // });

      let res = await getPhoneLogin({
        phone: this.phone,
        password: this.password,
      });

      // console.log(res);
      if (res.data.code === 200) {
        localStorage.setItem("cookie", res.data.cookie);
        this.$store.commit("updateIsLogin", true);
        let res1 = await getUser(res.data.account.id);
        let res2 = res1.data.profile;
        localStorage.setItem(
          "user",
          JSON.stringify({
            avatarUrl: res2.avatarUrl,
            backgroundUrl: res2.backgroundUrl,
            nickname: res2.nickname,
            userId: res2.userId,
            signature: res2.signature,
          })
        );
        // console.log(res2);
        this.$store.commit("updateUser", res1);
        this.$router.push("/home");
      } else {
        alert("手机号或密码错误");
        this.password = "";
      }
    },

    qrLogin: async function () {
      let timer;

      let key = await getKey();
      this.qrKey = key.data.data.unikey;
      // console.log(key);

      let res1 = await getQr(this.qrKey);
      this.img = res1.data.data.qrimg;
      // console.log(this.img);

      timer = setInterval(async () => {
        if (this.isShow == false) {
          clearInterval(timer);
        }
        const statusRes = await getQrcheck(this.qrKey);
        console.log(statusRes);
        if (statusRes.data.code === 800) {
          alert("二维码已过期,请重新获取");
          clearInterval(timer);
        }
        if (statusRes.data.code === 803) {
          clearInterval(timer);
          alert("登录成功");

          localStorage.setItem("cookie", statusRes.data.cookie);

          const cookie = localStorage.getItem("cookie");
          console.log(statusRes.data);

          let res2 = await getLoginStatus(cookie);
          console.log(res2);
          localStorage.setItem(
            "user",
            JSON.stringify({
              avatarUrl: res2.data.data.profile.avatarUrl,
              backgroundUrl: res2.data.data.profile.backgroundUrl,
              nickname: res2.data.data.profile.nickname,
              userId: res2.data.data.profile.userId,
              signature: res2.data.data.profile.signature,
            })
          );
          this.$router.push("/home");
        }
      }, 3000);
    },
  },
};
</script>

<style scoped>
.login {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.van-cell {
  background: #f5f5f5;
}
.qrtext {
  margin: 10px;
  color: #666666;
  border-bottom: 1px solid #666666;
}
.qrtext1 {
  margin: 12px auto 0 auto;
  color: #666666;
  border-bottom: 1px solid #666666;
}
.yktext {
  margin: 10px;
  color: #666666;
  border-bottom: 1px solid #666666;
}
</style>