<template>
  <div>
    <div class="MusicItemTop">
      <van-icon
        name="arrow-left"
        size="24"
        color="#000"
        @click="$router.go(-1)"
      />
    </div>
    <div class="recommenditem">
      <span class="icspan1">每日推荐</span>
      <div class="rcPlayAll">
        <van-icon
          style="display: flex; align-items: center"
          name="play-circle"
          color="red"
          size="40px"
          @click="playMusic(0)"
        >
          <span>播放全部</span>
        </van-icon>
      </div>

      <div
        @click="playMusic(i)"
        class="userMusicList"
        v-for="(item, i) in musicList"
        :key="i"
      >
        <div class="ml">
          <img class="rchead" :src="item.al.picUrl" alt="" />
          <div class="ml1">
            <span class="mlName">{{ item.name }}</span>
            <span class="mlauthor">{{ item.ar[0].name }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { getRecommend } from "../../request/api/item";

export default {
  data() {
    return {
      musicList: [],
    };
  },
  mounted() {
    this.getRecommendMusic();
  },
  methods: {
    getRecommendMusic: async function () {
      const cookie = localStorage.getItem("cookie");
      let res = await getRecommend(cookie);
      // console.log(res);
      this.musicList = res.data.data.dailySongs;
      // console.log(this.musicList);
    },
    playMusic: function (i) {
      this.updatePlayList(this.musicList);
      this.updatePlayListIndex(i);
    },
    ...mapMutations(["updatePlayList", "updatePlayListIndex"]),
  },
};
</script>

<style>
.rchead {
  width: 50px;
  border-radius: 8px;
}
.mlauthor {
  font-size: 14px;
  color: #717171;
}
.recommenditem {
  padding-bottom: 60px;
}
.rcPlayAll {
  margin: 30px 0 20px 10px;
}
.rcPlayAll span {
  color: black;
  margin-left: 10px;
  font-size: 23px;
  font-weight: 600;
}
</style>