import service from ".."
// 获取歌单详情
export function getMusicItemList(data) {
    return service({
        methos: "GET",
        url: `/playlist/detail?id=${data}`
    })
}
// 获取歌单所有歌曲
export function getItemList(data) {
    return service({
        methos: "GET",
        url: `/playlist/track/all?id=${data}&offset=0
        `
    })
}
// 获取歌词
export function getMusicLyric(data) {
    return service({
        method: "GET",
        url: `/lyric?id=${data}`
    })
}
//获取用户歌单
export function getUserMusicList(data) {
    return service({
        method: "GET",
        url: `/user/playlist?uid=${data}`
    })
}
//获取每日推荐
export function getRecommend(data) {
    return service({
        method: "GET",
        url: `/recommend/songs?cookie=${data}`
    })
}
// 获取已喜欢
export function getLiked() {
    return service({
        method: "GET",
        url: `/likelist?cookie=${localStorage.getItem('cookie')}`
    })
}