<template>
  <MusicItemHome :playlist="state.playlist" />
  <MusicItemList :itemList="state.itemList" />
</template>

<script>
import { useRoute } from "vue-router";
import { onMounted, reactive } from "vue";
import { getMusicItemList, getItemList } from "../request/api/item.js";
import MusicItemHome from "../components/items/MusicItemHome.vue";
import MusicItemList from "../components/items/MusicItemList.vue";
export default {
  components: {
    MusicItemHome,
    MusicItemList,
  },
  setup() {
    const state = reactive({
      // 歌单详情
      playlist: {},
      // 歌单歌曲
      itemList: [],
    });
    onMounted(async () => {
      let id = useRoute().query.id;
      //   console.log(id);
      // 获取歌单详情
      let res = await getMusicItemList(id);
      // console.log(res);
      state.playlist = res.data.playlist;
      // 获取歌单歌曲
      let res1 = await getItemList(id);
      // console.log(res1);
      state.itemList = res1.data.songs;
      // console.log(state.itemList);
      
      // 防止页面刷新造成数据丢失,将数据保存到本地
      sessionStorage.setItem("itemDetail", JSON.stringify(state));
    });
    return { state };
  },
};
</script>

