<template>
  <div>
    <div class="MusicItemTop">
      <van-icon
        name="arrow-left"
        size="24"
        color="#000"
        @click="$router.go(-1)"
      />
    </div>
    <span>榜单推荐</span>

    <div class="TopList">
      <div class="TopList1" v-for="item in topList" :key="item">
        <router-link :to="{ path: '/MusicItem', query: { id: item.id } }">
          <img class="tlhead" :src="item.coverImgUrl" alt="" />
        </router-link>
      </div>
    </div>

    <div>
      <span>精选榜单</span>
      <van-grid :border="false" :column-num="3" style="padding-top: 14px">
        <div class="TopList2" v-for="item in topList1" :key="item">
          <router-link :to="{ path: '/MusicItem', query: { id: item.id } }">
            <img class="tlhead" :src="item.coverImgUrl" alt="" />
          </router-link>
        </div>
      </van-grid>
    </div>
  </div>
</template>

<script>
import { geteMusicTopList } from "../../request/api/home";
export default {
  data() {
    return {
      topList: [],
      topList1: [],
    };
  },
  mounted() {
    this.getTopList();
  },
  methods: {
    getTopList: async function () {
      let res1 = await geteMusicTopList();
      //   console.log(res1);
      let res2 = [res1.data.list[1], res1.data.list[2], res1.data.list[3]];
      let res3 = [
        res1.data.list[5],
        res1.data.list[8],
        res1.data.list[9],
        res1.data.list[16],
        res1.data.list[22],
        res1.data.list[24],
        res1.data.list[21],
        res1.data.list[19],
        res1.data.list[20],
      ];
      //   console.log(res2);
      this.topList = res2;
      //   console.log(res3);
      this.topList1 = res3;
    },
  },
};
</script>

<style scoped>
.TopList {
  display: flex;
  justify-content: center;
}
.TopList1 {
  margin: 20px 10px;
}
.tlhead {
  width: 100px;
  border-radius: 15px;
}
.van-grid {
  justify-content: center;
}
.TopList2 {
  margin: 10px 10px;
}
span {
  justify-content: center;
  display: flex;
  font-weight: 600;
  font-size: 23px;
}
</style>