<template>
  <div class="cs">
    <span class="spanAll" @click="TgLogin">
      <img style="width: 22px" src="../assets/Tg.png" alt="" />
      <span class="text">切换账号</span>
    </span>
    <span class="spanAll" @click="exitLogin">
      <img style="width: 18px" src="../assets/ico-exit.png" alt="" />
      <span class="text1">退出登录</span>
    </span>
  </div>
</template>

<script>
import { logout } from "../request/api/home";
export default {
  methods: {
    exitLogin: async function () {
      let res = await logout();
      if (res.data.code === 200) {
        localStorage.clear();
        window.location.reload();
      }
    },
    TgLogin: async function () {
      let res = await logout();
      if (res.data.code === 200) {
        localStorage.clear();
        setTimeout(() => {
          window.location.reload();
        }, 250);
        this.$router.push("/login");
      }
    },
  },
};
</script>

<style>
.cs {
  margin: 20px;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
}
.cs .spanAll {
  display: flex;
  padding: 15px;
  align-items: center;
}
span .text {
  margin-left: 10px;
  font-size: 18px;
  color: #515151;
}
span .text1 {
  margin-left: 14px;
  font-size: 18px;
  color: #d81e06;
}
</style>