<template>
  <div>
    <div class="itemList">
      <van-icon
        name="play-circle"
        color="red"
        size="40px"
        @click="playMusic(0)"
      >
        <span class="icspan">播放全部</span>
      </van-icon>
      <div
        class="item"
        v-for="(item, i) in itemList"
        :key="i"
        @click="playMusic(i)"
      >
        <span class="num">{{ i + 1 }}</span>
        <div class="song">
          <span class="songname">{{ item.name }}</span>
          <div class="author">
            <span class="" v-for="(item1, i) in item.ar" :key="i">
              {{ item1.name }}<span v-if="i < item.ar.length - 1">/</span>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  setup(props) {
    // console.log(props);
  },
  props: ["itemList"],
  methods: {
    playMusic: function (i) {
      this.updatePlayList(this.itemList);
      this.updatePlayListIndex(i);
    },
    ...mapMutations(["updatePlayList", "updatePlayListIndex"]),
  },
};
</script>

<style>
.itemList {
  position: absolute;
  top: 378px;
  width: 100%;
  background-color: #fff;
  padding-bottom: 60px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.itemList .item {
  display: flex;
}
.itemList .item .num {
  display: flex;
  align-items: center;
  margin: 20px;
  font-size: 26px;
  color: #999999;

}
.song {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 20px 0;
}

/* .itemList .van-icon-bars {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 20px;
} */

.songname {
  margin-right: 20px;

  font-size: 18px;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
}
.author {
  margin-right: 20px;

  font-size: 15px;
  color: #999999;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
}
.itemList .van-icon-play-circle {
  margin: 20px 0 10px 22px;
  display: flex;
  align-items: center;
}
.icspan {
  color: black;
  margin-left: 20px;
  font-size: 23px;
  font-weight: 600;
}
</style>