<template>
  <div>
    <div class="Dttop">
      <van-icon
        name="arrow-down"
        color="#999999"
        size="24"
        @click="updateDetailShow"
      />
    </div>

    <div
      class="lyric"
      ref="lyric"
      v-show="isLyricShow"
      @click="isLyricShow = false"
    >
      <p
        v-for="item in lyric"
        :key="item"
        :class="{
          active:
            currentTime * 1000 >= item.time && currentTime * 1000 < item.pre,
        }"
      >
        {{ item.lrc }}
      </p>
    </div>

    <div v-show="!isLyricShow">
      <div class="musicdisk" @click="isLyricShow = true">
        <img
          class="headimg"
          :src="musicDetail.al.picUrl"
          :class="{ headimg_active: !isbtnShow, headimg_pauesd: isbtnShow }"
          alt=""
        />
        <img
          class="cizhen"
          :class="{ cizhen_active: !isbtnShow }"
          src="../../assets/磁针.png"
          alt=""
        />
        <img class="disk" src="../../assets/musicdisk.png" alt="" />
      </div>

      <div class="Dtbottom1">
        <div class="musicname">
          <span class="songname">{{ musicDetail.name }}</span>

          <div style="display: flex">
            <span class="author" v-for="(item, i) in musicDetail.ar" :key="i"
              >{{ item.name
              }}<span v-if="i < musicDetail.ar.length - 1"> /</span></span
            >
          </div>
        </div>

        <div class="lc">
          <van-icon v-show="!isLike" @click="like" name="like-o" size="28" />
          <van-icon
            name="like"
            @click="notlike"
            color="red"
            size="28"
            v-show="isLike"
          />
          <van-icon name="chat-o" size="28" />
        </div>
      </div>
    </div>

    <div class="Dtbottom2">
      <van-slider
        button-size="6px"
        inactive-color="#c2c2c2"
        active-color="#999"
        min="0"
        :max="duration"
        v-model="currentTime"
      />

      <!-- <input
        class="range"
        type="range"
        step="0.05"
        min="0"
        :max="duration"
        v-model="currentTime"
      /> -->

      <div class="runtime">
        <span>{{ formattedCurrentTime }}</span>
        <span>{{ formattedDuration }}</span>
      </div>
    </div>

    <div class="Dtbottom3">
      <img
        class="last"
        src="../../assets/icon/last.png"
        alt=""
        @click="toPlay(-1)"
      />
      <img
        class="play"
        src="../../assets/icon/播放2.png"
        alt=""
        @click="play"
        v-if="isbtnShow"
      />
      <img
        class="play"
        src="../../assets/icon/暂停.png"
        alt=""
        @click="play"
        v-else
      />
      <img
        class="next"
        src="../../assets/icon/next.png"
        alt=""
        @click="toPlay(1)"
      />
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from "vuex";
import { getLike, getNotLike } from "../../request/api/home";

export default {
  mounted() {
    // console.log(this.musicDetail);
    // console.log(this.lyricList.lyric);
    this.addDuration();
    // console.log(this.playList[this.playListIndex].id);
    // console.log(this.$store.state.userLike);
    // this.liked();
  },
  updated() {
    // this.liked();
    this.addDuration();
  },

  props: ["musicDetail", "isbtnShow", "addDuration"],

  // props: ["musicDetail", "isbtnShow", "play"],

  methods: {
    // updateDetailShow() {
    //   this.$emit("updateDetailShow");
    // },

    liked: async function () {
      let userLike = this.$store.state.userLike;
      // console.log(userLike);
      let id = this.playList[this.playListIndex].id;

      // 修改为箭头函数，这样可以访问到外部的 this
      const checkId = () => {
        // 检查 id 是否存在于 userLike 对象的键中
        if (userLike.includes(id)) {
          this.isLike = true;
          console.log("1");
        } else {
          this.isLike = false;
          console.log("2");
        }
      };

      checkId();
    },
    like: async function () {
      let id = this.playList[this.playListIndex].id;
      let res = await getLike(id);
      this.$store.dispatch("getUserLiked", id);
      this.isLike = !this.isLike;
      console.log(res);
    },
    notlike: async function () {
      let id = this.playList[this.playListIndex].id;
      let res = await getNotLike(id);
      this.$store.dispatch("getUserLiked", id);
      this.isLike = !this.isLike;
      console.log(res);
    },

    play() {
      this.$emit("play");
    },
    toPlay: function (num) {
      let index = this.playListIndex + num;
      if (index < 0) {
        index = this.playList.length - 1;
      } else if (index == this.playList.length) {
        index = 0;
      }
      this.updatePlayListIndex(index);
    },
    secondsToTime(i) {
      let minutes = Math.floor(i / 60);
      let seconds = Math.floor(i - minutes * 60);
      let formattedTime =
        (minutes < 10 ? "0" : "") +
        minutes +
        ":" +
        (seconds < 10 ? "0" : "") +
        seconds;
      return formattedTime;
    },

    ...mapMutations([
      "updateDetailShow",
      "updatePlayListIndex",
      "updateCurrentTime",
      "updateLiked",
    ]),
  },
  data() {
    return {
      isLyricShow: false,
      isLike: false,
    };
  },
  computed: {
    ...mapState([
      "lyricList",
      "currentTime",
      "playListIndex",
      "playList",
      "duration",
      "userLike",
    ]),
    // 对歌词进行分割
    lyric: function () {
      let arr;
      if (this.lyricList.lyric) {
        // /\r?\n/正则换行符
        arr = this.lyricList.lyric.split(/\r?\n/).map((item, i) => {
          let min = item.slice(1, 3);
          let sec = item.slice(4, 6);
          let mill = item.slice(7, 10);
          let lrc = item.slice(11, item.length);
          let time =
            parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          if (isNaN(Number(mill))) {
            mill = item.slice(7, 9);
            lrc = item.slice(10, item.length);
            time =
              parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          }
          if (isNaN(Number(mill))) {
            mill = item.slice(7, 8);
            lrc = item.slice(9, item.length);
            time =
              parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          }
          // console.log(min, sec, mill, lrc);

          return { min, sec, mill, lrc, time };
        });
        arr.forEach((item, i) => {
          if (i === arr.length - 1 || isNaN(arr[i + 1].time)) {
            item.pre = 100000;
          } else {
            item.pre = arr[i + 1].time;
          }
        });
      }
      // console.log(arr);
      return arr;
    },
    formattedDuration() {
      return this.secondsToTime(this.duration);
    },
    formattedCurrentTime() {
      return this.secondsToTime(this.currentTime);
    },
  },
  watch: {
    currentTime: function (newValue) {
      let p = document.querySelector("p.active");
      // console.log([p]);
      if (p) {
        if (p.offsetTop > 300) {
          this.$refs.lyric.scrollTop = p.offsetTop - 300;
        }
      }
      if (newValue === this.duration) {
        if (this.playListIndex === this.playList.length - 1) {
          this.updatePlayListIndex(0);
          this.play();
        } else {
          this.updatePlayListIndex(this.playListIndex + 1);
        }
      }
      // console.log([this.$refs.lyric]);
    },
    // 监听 playListIndex 的变化
    playListIndex: {
      handler(newIndex, oldIndex) {
        // 在索引变化时调用 liked 方法
        if (localStorage.getItem("cookie")) {
          this.liked();
        }
      },
      immediate: true, // 立即触发一次，以确保初始值也会触发
    },
  },
};
</script>

<style scoped>
.Dttop {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 22px;
}
.musicdisk {
  padding: 180px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.lyric {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: scroll;
  width: 100%;
  height: 438px;
  margin: 59px 0 59px 0;

  /* scroll-behavior: smooth; */
}

/* 隐藏滚动条 */
.lyric::-webkit-scrollbar {
  display: none;
}

.zw1 {
  height: 70%;
}
.zw2 {
  height: 100px;
}

.lyric p {
  text-align: center;
  font-size: 1.25rem;
  margin-left: 30px;
  margin-right: 30px;
  color: #999999;
}
.lyric .active {
  color: #000;
}
.musicdisk .disk {
  width: 300px;
  height: 300px;
  z-index: -1;
  position: absolute;
}

.musicdisk .cizhen {
  position: absolute;
  width: 198px;
  top: 6%;
  transform-origin: 50% 20px;
  transform: rotate(-30deg);
  transition: all 1s;
}
.musicdisk .cizhen_active {
  position: absolute;
  width: 198px;
  top: 6%;
  transform-origin: 50% 20px;
  transform: rotate(0);
  transition: all 1s;
}
.musicdisk .headimg {
  width: 142px;
  height: 142px;
  border-radius: 50%;
  animation: rotateheadimg 26s linear infinite;
}
.musicdisk .headimg_active {
  animation-play-state: running;
}
.musicdisk .headimg_pauesd {
  animation-play-state: paused;
}

@keyframes rotateheadimg {
  0% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(360deg);
  }
}
.Dtbottom1 {
  display: flex;
  justify-content: space-between;
  height: 54px;
}
.Dtbottom1 .lc {
  margin-right: 12px;
  align-items: center;
  display: flex;
}
.Dtbottom2 {
  margin: 0 14px;
  padding-top: 26px;
}

.runtime {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.runtime span {
  color: #999;
}

.Dtbottom3 {
  margin-top: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.last,
.next {
  width: 24px;
  height: 100%;
}
.play {
  margin: 10px 70px;
  width: 40px;
}
.van-icon-like-o,
.van-icon-like {
  margin: 0 30px;
}
.musicname {
  margin-left: 12px;

  display: inline-block;
  overflow: hidden;
  flex-direction: column;
  white-space: nowrap; /* 防止换行 */
}
.musicname .songname {
  /* font-weight: 600; */
  font-size: 22px;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
}
.musicname .author {
  margin-top: 4px;
  margin-right: 6px;
  color: #999;
}

.range {
  position: relative;
  width: 100%;
}
</style>