<template>
  <div class="MusicItemTop">
    <van-icon
      name="arrow-left"
      size="24"
      color="#fff"
      @click="$router.go(-1)"
    />
    <span class="Topspan">歌单</span>
    <van-icon name="search" size="24" color="#fff" />
    <img :src="playlist.coverImgUrl" alt="" class="bgImg" />
    <img :src="playlist.coverImgUrl" alt="" class="headImg" />
    <span class="name">{{ playlist.name }}</span>
    <!-- <span class="playCont">
      <img class="play1" src="../../assets/icon/play1.png" alt="" />
      {{ chageCount(playlist.playCount) }}
    </span> -->
    <img :src="playlist.creator.avatarUrl" alt="" class="creatorHead" />
    <span class="nickname">{{ playlist.creator.nickname }}</span>
    <div class="tags">
      <van-tag
        primary
        size="large"
        type="primary"
        color="rgba(144, 144, 144, 0.5)"
        v-for="(tag, index) in playlist.tags"
        :key="index"
        :style="{
          left: `${index * 10}px`,
        }"
        >{{ tag }}</van-tag
      >
    </div>

    <div class="topbtn" style="left: 4%" v-show="!show">
      <van-icon class="btn" name="share" color="#fff" size="24px" />
    </div>
    <div class="topbtn" style="left: 36%" v-show="!show">
      <van-icon name="chat" color="#fff" size="24px" />
    </div>
    <div class="topbtn" style="left: 68%" v-show="!show">
      <van-icon name="star" color="#fff" size="24px" />
    </div>

    <span
      :class="{ description: show, description1: !show }"
      class=""
      @click="isShow"
      >{{ playlist.description }}</span
    >
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
    };
  },
  methods: {
    isShow() {
      this.show = !this.show;
    },
  },
  setup(props) {
    // console.log(props);
    // 如果数据拿不到，就获取sessionStorage的数据
    if ((props.playlist.creator = "")) {
      props.playlist.creator = JSON.parse(
        sessionStorage.getItem().playlist
      ).creator;
    }
    function chageCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + "亿";
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
    }
    return { chageCount };
  },
  props: ["playlist"],
};
</script>

<style>
.MusicItemTop {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 22px;
  position: relative;
}

.MusicItemTop .van-icon-arrow-left {
  margin-left: 10px;
}
.MusicItemTop .van-icon-search {
  margin-right: 10px;
}
.MusicItemTop .Topspan {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
}

.MusicItemTop .headImg {
  position: absolute;
  left: 26px;
  top: 66px;
  width: 150px;
  height: 150px;
  border-radius: 15px;
  object-fit: cover;
  margin: 0 auto;
}
.MusicItemTop .bgImg {
  width: 100%;
  height: 800px;
  position: absolute;
  z-index: -1;
  filter: blur(100px);
}
.MusicItemTop .name {
  position: absolute;
  left: 190px;
  top: 60px;
  color: #fff;
  font-size: 22px;
  font-weight: 400;
  margin-right: 20px;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
}
.MusicItemTop .playCont {
  position: absolute;
  top: 70px;
  left: 80px;
  color: rgb(255, 255, 255);
  padding: 4px 8px;
  font-size: 17px;
  font-weight: 600;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.MusicItemTop .play1 {
  width: 12px;
}

.MusicItemTop .creatorHead {
  position: absolute;
  left: 190px;
  top: 130px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
}
.MusicItemTop .nickname {
  font-size: 18px;
  font-weight: 600;
  position: absolute;
  color: #505050;
  left: 234px;
  top: 136px;
  margin-right: 7px;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1; /* 显示两行文本 */
}
.MusicItemTop .description1 {
  font-size: 17px;
  font-weight: 600;
  position: absolute;
  color: #505050;
  left: 26px;
  top: 236px;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  margin-right: 20px;
}
.MusicItemTop .description {
  font-size: 17px;
  font-weight: 600;
  position: absolute;
  color: #505050;
  left: 26px;
  top: 236px;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 5;
  margin-right: 20px;
}
.MusicItemTop .tags {
  position: absolute;
  left: 193px;
  top: 180px;
}

.MusicItemTop .topbtn {
  top: 280px;
  width: 90px;
  height: 26px;
  position: absolute;
  align-items: center;
  background-color: rgba(144, 144, 144, 0.5);
  border-radius: 50px;
  padding: 10px;
}
.MusicItemTop .van-icon-share,
.MusicItemTop .van-icon-star,
.MusicItemTop .van-icon-chat {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>