import servic from "..";

// 获取轮播图数据
export function getBanner() {
    return servic({
        method: "GET",
        url: "/banner?type=2"
    })
}
// 获取推荐音乐数据
export function getMusicList() {
    return servic({
        method: "GET",
        // url: `/recommend/resource?cookie=${localStorage.getItem("cookie")}`
        url: "/personalized?limit=6"
    })
}

// 获取推荐音乐数据(用户)
export function getMusicList1() {
    return servic({
        method: "GET",
        url: `/recommend/resource?cookie=${localStorage.getItem("cookie")}`
    })
}
// 搜索
export function getSearch(data) {
    return servic({
        method: "GET",
        url: `/cloudsearch?keywords=${data}`
    })
}
// 获取二维码key
export function getKey() {
    return servic({
        url: `/login/qr/key?timestamp=${Date.now()}`
    })
}
// 生成二维码
export function getQr(data) {
    return servic({
        url: `/login/qr/create?key=${data}&qrimg=true&timestamp=${Date.now()}`
    })
}
// 查询二维码状态
export function getQrcheck(data) {
    return servic({
        method: "GET",
        url: `/login/qr/check?key=${data}&timestamp=${Date.now()}`
    })
}
// 查询登录状态
export function getLoginStatus(cookie = '') {
    return servic({
        method: "POST",
        url: `/login/status?timestamp=${Date.now()}`,
        data: {
            cookie
        }
    })
}
// 手机号登录
export function getPhoneLogin(data) {
    return servic({
        url: `/login/cellphone?phone=${data.phone}&password=${data.password}`
    })
}
// 退出登录
export function logout() {
    return servic({
        url: "/logout"
    })
}
// 获取用户详情
export function getUser(data) {
    return servic({
        url: `/user/detail?uid=${data}`
    })
}
// 喜欢
export function getLike(data) {
    return servic({
        url: `/like?id=${data}&like=true&cookie=${localStorage.getItem('cookie')}`
    })
}
// 不喜欢
export function getNotLike(data) {
    return servic({
        url: `/like?id=${data}&like=false&cookie=${localStorage.getItem('cookie')}`
    })
}
// 获取音乐url
export function geteMusicUrl(data) {
    return servic({
        url: `/song/url?id=${data}&cookie=${localStorage.getItem('cookie')}`
    })
}
// 获取排行榜
export function geteMusicTopList() {
    return servic({
        url: "/toplist"
    })
}
// 获取推荐歌单
export function getePersonaLized() {
    return servic({
        url: `/personalized?limit=18&cookie=${localStorage.getItem('cookie')}`
    })
}
// 私人FM
export function geteFM() {
    return servic({
        url: `/personal_fm?cookie=${localStorage.getItem('cookie')}`
    })
}
//获取歌曲详情
export function geteSong(data) {
    return servic({
        url: `/song/detail?ids=${data}`
    })
}