<template>
  <div>
    <div class="top Sctop">
      <van-icon
        name="arrow-left"
        size="24"
        color="#000"
        @click="$router.go(-1)"
      />
      <van-search
        v-model="searchKey"
        shape="round"
        background="#fff"
        placeholder="请输入搜索关键词"
        to="/search"
      />
      <div style="margin-right: 10px" @click="getMusicSearch">搜索</div>
    </div>
    <div class="ScitemList">
      <div class="item" v-for="item in searchList" :key="item">
        <div class="song" @click="updateIndex(item)">
          <span class="Scsongname">{{ item.name }}</span>
          <span
            class="Scauthor"
            v-for="(item1, index) in item.ar"
            :key="index"
            >{{ item1.name }}</span
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSearch } from "@/request/api/home";
export default {
  data() {
    return {
      searchKey: "",
      searchList: [],
    };
  },
  methods: {
    getMusicSearch: async function () {
      let res = await getSearch(this.searchKey);
      this.searchList = res.data.result.songs;
      // console.log(this.searchList);
    },
    updateIndex: function (item) {
      this.$store.commit("pushPlayList", item);
      this.$store.commit(
        "updatePlayListIndex",
        this.$store.state.playList.length - 1
      );
    },
  },
};
</script>

<style scoped>
.ScitemList {
  margin-left: 16px;
  padding-bottom: 60px;
}

.Scsongname {
  font-size: 16px;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
}
.Scauthor {
  font-size: 12px;
  color: #999999;
}

.van-icon-arrow-left {
  margin-left: 10px;
}
</style>