<template>
  <div class="musicContent">
    <van-swipe
      :loop="false"
      :width="150"
      :show-indicators="false"
      class="musicSwipe"
    >
      <van-swipe-item v-for="(item, i) in state.musicList" :key="i">
        <!-- 路由跳转并且携带id -->
        <router-link
          class="van-swipe-item"
          :to="{ path: '/MusicItem', query: { id: item.id } }"
        >
          <img class="pic" :src="item.picUrl" alt="" />
          <img class="play" src="../assets/icon/play.png" alt="" />
          <span class="playCont">
            <img class="play1" src="../assets/icon/play1.png" alt="" />
            {{ chageCount(item.playCount) }}
            {{ chageCount(item.playcount) }}
          </span>
          <span class="name">{{ item.name }}</span>
        </router-link>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { getMusicList, getMusicList1 } from "../request/api/home.js";
import { reactive, onMounted } from "vue";

export default {
  // vue2写法

  // data() {
  //   return {
  //     musicList: [],
  //   };
  // },
  // methods: {
  //   async getGnedan() {
  //     let res = await getMusicList();
  //     console.log(res);
  //   },
  //   chageCount: function (num) {
  //     if (num >= 100000000) {
  //       return (num / 100000000).toFixed(1) + "亿";
  //     } else if (num >= 10000) {
  //       return (num / 10000).toFixed(1) + "万";
  //     }
  //   },
  // },
  // mounted() {
  //   this.getGnedan();
  // },

  // vue3写法

  setup() {
    const state = reactive({
      musicList: [],
    });
    function chageCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + "亿";
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }else if (num < 10000) {
        return (num)
      }
    }
    onMounted(async () => {
      if (localStorage.getItem("cookie")) {
        let res = await getMusicList1();
        // console.log(res);
        state.musicList = res.data.recommend;
      } else {
        let res = await getMusicList();
        // console.log(res);
        state.musicList = res.data.result;
        console.log(res);
      }
    });
    return { state, chageCount };
  },
};
</script>

<style scoped>
.musicContent {
  margin: 7px 0;
  width: 100%;
}
.musicContent .musicSwipe {
  height: 100%;
}
.musicContent .musicSwipe .van-swipe-item {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.musicContent .musicSwipe .pic {
  padding-top: 5px;
  background-color: rgb(234, 236, 239);
  width: 135px;
  height: 140px;
  border-radius: 15px;
  object-fit: cover;
  margin: 0 auto;
}

.musicContent .musicSwipe .name {
  margin: 5px 8px;
  font-size: 18px;
  text-align: center;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2; /* 显示两行文本 */
}

.musicContent .musicSwipe .play {
  width: 26px;

  position: absolute;
  top: 105px;
  right: 12px;
}
.musicContent .musicSwipe .play1 {
  width: 12px;
}

.musicContent .musicSwipe .playCont {
  position: absolute;
  top: 8px;
  right: 16px;
  color: rgb(255, 255, 255);
  font-size: 17px;
  font-weight: 600;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
</style>