<template>
  <div id="topSwpie">
    <van-swipe :autoplay="3000" lazy-render>
      <van-swipe-item v-for="image in state.images" :key="image">
        <img width="100%" :src="image.pic" />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { getBanner } from "@/request/api/home.js";
import { reactive, onMounted } from "vue";

export default {
  // vue3写法
  setup() {
    const state = reactive({
      images: [],
    });
    onMounted(async () => {
      let res = await getBanner();
      // console.log(res);  
      state.images = res.data.banners;
    });
    return { state };
  },
};
</script>

<style>
#topSwpie .van-swipe {
  margin: 7px 0;
}
#topSwpie .van-swipe .van-swipe-item {
  height: 100%;
  display: flex;
  justify-content: center;
}
#topSwpie .van-swipe .van-swipe-item img {
  border-radius: 15px;
  object-fit: cover;  
  width: 90%;
}

#topSwpie .van-swipe__indicator {
  width: 15px;
  height: 4px;
  border-radius: 15px;
}
:root:root {
  --van-swipe-indicator-active-background: rgb(255, 255, 255);
}
</style>