import { createRouter, createWebHashHistory } from 'vue-router'
import store from '@/store/index.js'

import Login from '../views/Login.vue'
import Home from '../views/Home.vue'
import My from '../views/My.vue'
import MusicItem from '../views/MusicItem.vue'
import Search from '../views/Search.vue'
import RecommendItem from '../components/items/RecommendItem.vue'
import TopList from '../components/items/TopList.vue'
import Persona from '@/components/items/Persona.vue'


const router = createRouter({
  history: createWebHashHistory(),
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      beforeEnter: (to, from, next) => {
        if (store.state.isLogin || localStorage.getItem('cookie')) {
          next()
        } else {
          next('/login')
        }
      },
      path: '/my',
      name: 'my',
      component: My
    },
    {
      path: '/musicitem',
      name: 'musicitem',
      component: MusicItem
    },
    {
      path: '/search',
      name: 'search',
      component: Search
    },
    {
      path: '/recommenditem',
      name: 'recommenditem',
      component: RecommendItem
    },
    {
      path: '/toplist',
      name: 'toplist',
      component: TopList
    },
    {
      path: '/persona',
      name: 'persona',
      component: Persona
    },

  ]
})

export default router
