<template>
  <div style="height: 800px">
    <div class="MusicItemTop">
      <van-icon
        name="arrow-left"
        size="24"
        color="#000"
        @click="$router.go(-1)"
      />
    </div>
    <span class="toptext">更多歌单推荐</span>
    <van-grid :border="false" :column-num="3" style="padding-top: 14px">
      <div class="TopList2" v-for="item in topList" :key="item">
        <router-link :to="{ path: '/MusicItem', query: { id: item.id } }">
          <img class="psnhead" :src="item.picUrl" alt="" />
          <span class="itemname" style="width: 100px">{{ item.name }}</span>
        </router-link>
      </div>
    </van-grid>

    <span class="toptext">这些也不容错过</span>

    <van-swipe
      :loop="false"
      :width="110"
      :show-indicators="false"
      class="musicSwipe"
    >
      <van-swipe-item v-for="item in topList1" :key="item">
        <!-- 路由跳转并且携带id -->
        <router-link
          class="van-swipe-item"
          :to="{ path: '/MusicItem', query: { id: item.id } }"
        >
          <img class="psnhead" :src="item.picUrl" alt="" />
          <span class="itemname1" style="margin: 0px 8px">{{ item.name }}</span>
        </router-link>
      </van-swipe-item>
    </van-swipe>

    <van-swipe
      :loop="false"
      :width="110"
      :show-indicators="false"
      class="musicSwipe"
    >
      <van-swipe-item v-for="item in topList2" :key="item">
        <!-- 路由跳转并且携带id -->
        <router-link
          class="van-swipe-item"
          :to="{ path: '/MusicItem', query: { id: item.id } }"
        >
          <img class="psnhead" :src="item.picUrl" alt="" />
          <span class="itemname1" style="margin: 0px 8px">{{ item.name }}</span>
        </router-link>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>
<script>
import { getePersonaLized } from "../../request/api/home";

export default {
  data() {
    return {
      topList: [],
      topList1: [],
      topList2: [],
    };
  },
  mounted() {
    this.getPersonaMusic();
  },
  methods: {
    getPersonaMusic: async function () {
      let res = await getePersonaLized();
      let result = res.data.result.slice(0, 6);
      //   console.log(result);
      this.topList = result;
      let result1 = res.data.result.slice(6, 12);
      //   this.topList = result1;
      this.topList1 = result1;
      //   console.log(result1);
      let result2 = res.data.result.slice(12, 18);
      this.topList2 = result2;
      //   console.log(result2);
    },
  },
};
</script>
<style scoped>
.TopList {
  display: flex;
  justify-content: center;
}

.psnhead {
  width: 100px;
  border-radius: 15px;
}

.van-grid {
  justify-content: center;
}

.TopList2 {
  margin: 10px 10px;
}

.toptext {
  justify-content: center;
  display: flex;
  font-weight: 600;
  font-size: 23px;
}
.itemname,
.itemname1 {
  font-size: 14px;
  text-align: center;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2; /* 显示两行文本 */
}
.van-swipe-item {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.van-swipe-item img {
  margin: 0 auto;
}
.musicSwipe {
  margin: 14px 0;
}
</style>